import React from "react"

import Sidebar from "./components/Sidebar"
import Header from "./components/Header"

import { BrowserRouter as Router, Route, Switch } from "react-router-dom"

import Home from "./pages/Home"
import GetStarted from "./pages/GetStarted"
import Redux from "./pages/Redux"
import Autobind from "./pages/Autobind"
import Splitter from "./pages/Splitter"

import "./styles/App.scss"

function App() {
    return (
        <div className='App'>
            <Header />
            <Router>
                <div className='wrapper'>
                    <Sidebar />
                    <div className='content'>
                        <Switch>
                            <Route path='/' exact component={Home} />
                            <Route path='/get-started' exact component={GetStarted} />
                            <Route path='/redux-and-reduxers' exact component={Redux} />
                            <Route path='/splitter' exact component={Splitter} />
                            <Route path='/autobind' exact component={Autobind} />
                        </Switch>
                    </div>
                </div>
            </Router>
        </div>
    )
}

export default App
